@import url('https://fonts.googleapis.com/css2?family=Catamaran:wght@100..900&family=Montserrat:ital,wght@0,100..900;1,100..900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  transition: all 0.3s ease;
}

a, h1, h2, h3, h4, h5, h6, p {
  overflow-wrap: break-word;
}

a {
  text-decoration: none;
  color: inherit;
}

/* ROOT COLORS */
:root {
  --primary-blue: #0d6efd;
  --primary-gray: #6c757d;
  --primary-gold: #ffc107;
  --primary-light: #f8f9fa;
  --primary-dark: #212529;
  --secondary-blue: #dfe8f8;
  --secondary-gray: #343a40;
}

.fade-up {
  position: relative;
  animation: fade-up 700ms forwards ease-out;
  animation-delay: var(--animation-delay);
  opacity: 0;
  bottom: -60px;
}
@keyframes fade-up {
  0% {
    opacity: 0;
    bottom: -60px;
  }
  60% {
    opacity: .7;
    bottom: 4px;
  }
  100% {
    opacity: 1;
    bottom: 0px;
  }
}

.fade-right {
  position: relative;
  animation: fade-right 700ms forwards ease-out;
  opacity: 0;
  right: -60px;
}
@keyframes fade-right {
  0% {
    opacity: 0;
    right: -60px;
  }
  60% {
    opacity: 0.7;
    right: 4px;
  }
  100% {
    opacity: 1;
    right: 0px;
  }
}


/* MOBILE STYLING (SET TO DISPLAY NONE) */
.hamburgerMenu {
  display: none;
}
.side-bar-mobile {
  display: none;
}
.mobile-header {
  display: none;
}

/* MAIN STYLING */
.main {
  width: 100%;
  height: fit-content;
  display: block;
}

/* HEADER */
header {
  width: 100%;
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 0;
}
header .first-header {
  width: 100%;
  height: fit-content;
  padding: 20px 100px;
  background-color: var(--primary-blue);
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
}
header .first-header p {
  color: var(--primary-light);
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 400;
}
header .first-header .social-icons {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  gap: 20px;
}
header .first-header  i {
  color: var(--primary-light);
  font-size: 18px;
}
header .second-header {
  width: 100%;
  height: fit-content;
  padding: 5px 100px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
}
header .second-header .s-right {
  width: fit-content;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  gap: 40px;
}
header .second-header .s-left img {
  width: 150px;
  height: 80px;
  object-fit: contain;
}
header .second-header .s-right .call-now {
  width: fit-content;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
header .second-header .call-now i {
  color: var(--primary-blue);
  font-size: 30px;
}
.fa-phone-flip:before {
  content: "\f879";
}
header .second-header .call-now .s-right-column {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
header .second-header .call-now .s-right-column h5 {
  font-family: 'Catamaran', sans-serif;
  font-size: 18px;
  color: var(--primary-dark);
}
header .second-header .call-now .s-right-column h6 {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: var(--primary-gray);
}

/* NAVBAR */
nav {
  position: sticky;
  top: 0;
  width: 100%;
  height: fit-content;
  padding: 0px 100px;
  background-color: var(--secondary-gray);
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  z-index: 10;
}
nav.sticky {
  position: fixed;
  top: 0;
  z-index: 999; 
  width: 100%;
  opacity: 0.9;

}
nav .navlinks {
  width: fit-content;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  gap: 40px;
  list-style-type: none;
}
nav .navlinks li a {
  font-size: 18px;
  font-weight: 500;
  color: var(--primary-light);
  font-family: 'Catamaran', sans-serif;
  transition: all 0.3s ease;
}
nav .navlinks li a:hover, 
nav .navlinks li a:active {
  color: var(--primary-blue);
}


/* SEARCH INPUT */
nav .container {
  position: relative;
  box-sizing: border-box;
  width: fit-content;
}
nav .mainbox {
  box-sizing: border-box;
  position: relative;
  width: 230px;
  height: 70px;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  background-color: var(--primary-blue);
  transition: all 0.3s ease;
}
nav .checkbox:focus {
  border: none;
  outline: none;
}
nav .checkbox:checked {
  right: 10px;
}
nav .checkbox:checked ~ .mainbox {
  width: 80px;
}
nav .checkbox:checked ~ .mainbox .search_input {
  width: 0;
  height: 0px;
}
nav .checkbox:checked ~ .mainbox .iconContainer {
  padding-right: 8px;
}
nav .checkbox {
  box-sizing: border-box;
  width: 50px;
  height: 50px;
  position: absolute;
  right: 17px;
  top: 10px;
  z-index: 9;
  cursor: pointer;
  appearance: none;
}
nav .search_input {
  box-sizing: border-box;
  height: 100%;
  width: 170px;
  background-color: transparent;
  border: none;
  outline: none;
  padding-bottom: 4px;
  padding-left: 10px;
  font-size: 1.2em;
  color: white;
  transition: all 0.3s ease;
  font-weight: 500;
  font-family: 'Catamaran', sans-serif;
}
nav .search_input::placeholder {
  color: var(--primary-light);
  font-weight: 300;
  font-size: 14px;
}
nav .iconContainer {
  box-sizing: border-box;
  padding-top: 5px;
  width: fit-content;
  transition: all 0.3s ease;
}
nav .search_icon {
  box-sizing: border-box;
  fill: white;
  font-size: 1.3em;
}

