/* MOBILE (SMALL - LARGE) */
@media (max-width: 480px) {
    :root {
        --primary-blue: #0d6efd;
        --primary-gray: #6c757d;
        --primary-gold: #ffc107;
        --primary-light: #f8f9fa;
        --primary-dark: #212529;
        --secondary-blue: #dfe8f8;
        --secondary-gray: #343a40;
    }

    nav {
        display: none;
    }
    header {
        display: none;
    }
    .mobile-header {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: fit-content;
        padding: 20px;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-items: center;
        z-index: 50;
        background-color: var(--primary-light);
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    }
    .mobile-header img {
        width: 150px;
        object-fit: cover;
    }
    .hamburgerMenu {
        cursor: pointer;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
    }
    .hamburgerMenu .bar {
        width: 30px;
        height: 4px;
        border-radius: 5px;
        background-color: var(--primary-blue);
        margin: 2px 0;
        transition: transform 0.3s, opacity 0.3s;
    }
    .hamburgerMenu .bar.change:nth-child(1) {
        transform: translateY(5px) rotate(45deg);
    }
    .hamburgerMenu .bar.change:nth-child(2) {
        opacity: 0;
    }
    .hamburgerMenu .bar.change:nth-child(3) {
        transform: translateY(-10px) rotate(-45deg);
    }

    /* SIDE BAR MOBILE */
    .side-bar-mobile {
        display: none;
        background-color: var(--primary-light);
        width: 250px;
        height: 100vh !important;
        padding: 20px;
        overflow-y: scroll;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 20px;
        position: absolute;
        top: 0;
        left: -250px; 
        transition: left 0.3s ease-in-out;
        z-index: 1000;
    }
    .side-bar-mobile.open {
        left: 0; 
    }
    .side-bar-mobile::-webkit-scrollbar {
        display: none;
    }
    .side-bar-mobile img {
        width: 50px;
        height: 50px;
        object-fit: cover;
    }
    .side-bar-mobile h3 {
        font-family: Lobster Two, sans-serif;
        font-size: 25px;
        color: var(--primary-color-light);
    }
    .side-bar-mobile .sidebar-navlinks {
        width: 100%;
        height: fit-content;
        list-style: none;
        text-align: center;
    }
    .side-bar-mobile .sidebar-navlinks ul {
        list-style-type: none;
        /* padding-left: 10px; */
        transition: left 0.3s ease-in-out;
    }
    .side-bar-mobile .sidebar-navlinks li {
        width: 100%;
        padding: 10px;
    }
    .side-bar-mobile .sidebar-navlinks li a {
        width: 100%;
        font-size: 18px;
        font-weight: 700;
        font-family: 'Catamaran', sans-serif;
        text-decoration: none;
        text-align: left;
        color: var(--primary-color-light);
    }
    .side-bar-mobile .sidebar-navlinks button {
        background: none;
        border: none;
        color: var(--primary-dark);
        cursor: pointer;
        text-align: left;
        width: 100%;
        font-size: 18px;
        font-weight: 700;
        font-family: 'Catamaran', sans-serif;
        text-align: center;
        transition: left 0.3s ease-in-out;
    }

    /* RIGHT SIDE STYLING */
    .rightSide {
        width: 100%;
    }

    /* WELCOME SECTION (LANDING PAGE) */
    .welcome-section {
        margin-top: 80px;
        height: fit-content;
        justify-content: flex-start;
    }
    .welcome-section .welcome-caption {
        padding: 50px 20px;
        gap: 10px;
    }
    .welcome-section .welcome-caption h3 {
        font-size: 1.5em;
    }
    .welcome-section .welcome-caption h1 {
        font-size: 3em;
        line-height: 40px;
    }
    .welcome-section .welcome-caption p {
        font-size: 14px;
        width: 100%;
    }
    .welcome-section .intro-buttons {
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .welcome-section .intro-buttons button {
        padding: 10px;
        width: 60%;
        font-size: 12px;
    }
    .welcome-section .intro-buttons button:nth-of-type(1) {
        padding: 10px;
        width: 40%;
        font-size: 14px;
    }
    .welcome-section .modal-content {
        width: 80%;
        height: 30%;
        border-radius: 5px;
    }
    .welcome-section .video-frame {
        border-radius: 5px;
    }
    .welcome-section .energy-cards {
        bottom: -280px;
        padding: 0 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .welcome-section .energy-cards .energy-solutions {
        width: 100%;
        padding: 20px;
    }
    .welcome-section .energy-cards .energy-solutions h6 {
        font-size: 20px;
    }
    .welcome-section .energy-cards .energy-solutions p {
        font-size: 14px;
    }

    /* OUR SERVICES (LANDING PAGE) */
    .our-services {
        margin-top: 340px;
        padding: 20px;
        margin-bottom: 30px;
    }
    .our-services .services-caption h5 {
        font-size: 1.5em;
    }
    .our-services .services-caption h1 {
        font-size: 3em;
        text-align: center;
        line-height: 40px;
    }
    .our-services .service-card-list {
        margin-top: 40px;
        margin-bottom: 50px;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        gap: 100px; 
    }
    .our-services .services-card-container {
        flex-grow: 0; 
        max-width: 100%; 
    }
    .our-services .service-card-image {
        width: 100%;
        height: 300px;
    }
    .our-services .service-card-caption {
        bottom: -40px;
        width: 90%; 
        padding: 20px;
    }
    .our-services .service-card-caption h5 {
        font-size: 14px;
    }
    .our-services .service-card-caption h6 {
        font-size: 20px;
    }

    /* ABOUT US (LANDING PAGE) */
    .about-us {
        padding: 20px;
        align-items: flex-start;
        margin-bottom: 200px;
    }
    .about-us .about-us-caption {
        width: 100%;
        margin-bottom: 100px;
    }
    .about-us .about-us-caption h6 {
        font-size: 1.5em;
        margin-bottom: 0px;
    }
    .about-us .about-us-caption h1 {
        font-size: 3em;
        line-height: 40px;
    }
    .about-us .about-us-caption p {
        font-size: 16px;
    }
    .about-us .about-us-demograph {
        gap: 20px;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }
    .about-us .about-us-demograph .demograph {
        gap: 20px;
    }
    .about-us .demograph .demograph-caption {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
    }
    .about-us .demograph .demograph-caption h4 {
        font-size: 30px;
    }
    .about-us .demograph .demograph-caption h5 {
        font-size: 14px;
    }
    .about-us .call-us {
        width: 100%;
        padding: 20px;
        flex-direction: column;
        bottom: -280px;
        width: 100%; 
    }
    .about-us .call-us div {
        width: 100%;
    }
    .about-us .call-us div h2 {
        font-size: 2.5em;
    }
    .about-us .call-us div h5 {
        font-size: 1.5em;
    }
    .about-us .call-us img {
        width: 100%;
    }

    /* OUR TEAM (LANDING PAGE) */
    .our-team {
        margin-top: 320px;
        width: 100%;
    }
    .our-team .our-team-caption h1 {
        font-size: 3em;
    }
    .our-team .our-team-caption h5 {
        font-size: 1.5em;
    }
    .our-team .team-carousel-container{
        margin-top: 20px;
        width: 100%;
        height: fit-content;
        padding: 20px;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        align-items: center;
        justify-content: center;
        --animation-delay: 1200ms;
    }
    .our-team .team-carousel-container .splide__slide {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 10px;
        --animation-delay: 1300ms;
        background-color: var(--primary-light);
        position: relative;
    }
    .our-team .team-carousel-container .splide__slide > li {
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    }
    .our-team .team-carousel-container .splide__slide img {
        width: 200px;
        height: 200px;
        border-radius: 50%;
        object-fit: cover;
    
    }
    .our-team .team-carousel-container .splide__slide h2 {
        font-size: 24px;
        font-weight: 800;
        font-family: 'Catamaran', sans-serif;
        color: var(--primary-dark);
        transition: color 0.3s ease;
    }
    .our-team .team-carousel-container .splide__slide p { 
        font-size: 18px;
    }

    /* WHY CHOOSE US */
    .choose-us {
        padding: 20px;
    }
    .choose-us .choose-us-caption h5 {
        font-size: 1.5em;
    }
    .choose-us .choose-us-caption h1 {
        font-size: 3em;
        line-height: 40px;
    }
    .choose-us .choose-us-left {
        width: 100%;
    }
    .choose-us .choose-us-right {
        width: 100%;
    }
    .choose-us .choose-us-right img {
        height: 300px;
    }
    .choose-us .choose-us-left .choose-us-list li {
        padding-left: 40px;
    }
    .choose-us-lists li::after {
        top: 20%;
        font-size: 22px; 
        width: 20px;
        height: 20px;
        padding: 5px;
    }
    .choose-us .choose-us-left .choose-us-list h6 {
        font-size: 18px;
    }
    .choose-us .choose-us-left .choose-us-list p {
        font-size: 14px;
    }
    
    /* OUR PRICING */
    .our-pricing {
        padding: 20px;
    }
    .our-pricing .pricing-caption h5 {
        font-size: 1.5em;
    }
    .our-pricing .pricing-caption h1 {
        font-size: 3em;
        line-height: 40px;
    }
    .our-pricing .pricing-container {
        flex-wrap: wrap;
        justify-content: center;
    }
    .our-pricing .pricing-container .pricing-card {
        flex-grow: 0; 
        max-width: 100%; 
    }
    .our-pricing .pricing-card .pricing-card-caption h3 {
        font-size: 28px;
    }
    .our-pricing .pricing-card .pricing-description {
        padding: 20px;
    }
    .our-pricing .pricing-description .join-button {
        width: 100%;
        font-size: 14px;
    }

    /* TESTIMONIAL */
    .testimonials {
        margin-top: 20px;
        padding: 20px;
    }
    .testimonials .testimonial-caption h5 {
        font-size: 1.5em;
    }
    .testimonials .testimonial-caption h1 {
        font-size: 3em;
        line-height: 40px;
    }
    .testimonials .testimonial-container {
        margin-top: 30px;
        width: 100%;
        height: 500px;
        gap: 30px;
        flex-direction: column;
        justify-content: center ;
        align-items: flex-start;
    }
    .testimonials .testimonial-right {
        width: 100%;
        background-size: contain;
    }
    .testimonials .testimonial-left {
        width: 100%;
        height: 100%;
        padding: 10px 0;
    }
    .testimonial-splide .ratings .fa-star {
        font-size: 25px;
    }
    .testimonial-splide .ratings .fa-star-half-alt{
        font-size: 25px;
    }
    .testimonials .testimonial-left .testimonial-splide p {
        font-size: 14px;
    }
    
}

